.modal-picker-item-radio {
  display: block;
  height: 40px;
  font-size: 16px;
  line-height: 40px;
  text-align: left;
  border-bottom: 1px solid #eee;
  position: relative;
}
.modal-picker-item-radio .am-radio {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40px;
}
.modal-picker-item-radio .am-radio .am-radio-inner {
  top: 10px;
}
