.parts-result .result-title {
  font-size: 18px;
  margin: 10px 0;
}
.parts-result .am-list-item .am-list-line .am-list-extra {
  flex-basis: auto !important;
}
.parts-result .swipe-wrap {
  position: relative;
}
.parts-result .swipe-wrap.active .swipe-cover {
  left: -60px;
}
.parts-result .swipe-wrap .swipe-cover {
  position: relative;
  transition: all 0.25s;
  left: 0;
  z-index: 10;
}
.parts-result .swipe-wrap .swipe-content {
  z-index: 1;
  background-color: red;
  color: white;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 60px;
  display: flex;
  align-items: center;
  margin: 1px;
}
.parts-result .swipe-wrap .swipe-content div {
  text-align: center;
  flex: 1 1;
}
.modal-list .am-list-body::before {
  display: none !important;
}
